.timeline {
  position: relative;
  width: 100%;
  height: 24px;
  background: #ff325f; /* Old browsers */
  background: -moz-linear-gradient(45deg, #ff325f 0%, #1d19ff 100%);
  background: -webkit-linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
  background: linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
  overflow: hidden;

  &-inner {
    position: absolute;
    background-color: #000;
    height: 100%;
    width: 100%;
    top: 0;
    left: 1px; //fix bug in Opera with no-rendered-gradient
  }
  &-over {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
  }
  &-time {
    position: absolute;
    margin: 0;
    height: 24px;
    line-height: 22px;
    right: 4px;
    color: #fff;
  }
}