.mz-volume {
  display: flex;
  width: 200px;
  margin: auto;
  height: 100%;
  &-button {
    background: none;
    border: none;
    height: 64px;
    width: 64px;
    cursor: pointer
  }
  &-icon-svg {
    #mz-v-i_mute {
      visibility: hidden;
    }
    &.mid {
      #mz-v-i_max {
        visibility: hidden;
      }
    }
    &.low {
      #mz-v-i_max, #mz-v-i_mid  {
        visibility: hidden;
      }
    }
    &.zero {
      #mz-v-i_max, #mz-v-i_mid, #mz-v-i_low  {
        visibility: hidden;
      }
    }
    &.muted {
      #mz-v-i_max, #mz-v-i_mid, #mz-v-i_low  {
        visibility: hidden;
      }
      #mz-v-i_mute {
        visibility: visible;
      }
    }
  }
  &-input {
 
    @mixin track-color() {
      background: -moz-linear-gradient(45deg, #ff325f 0%, #1d19ff 100%);
      background: -webkit-linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
      background: linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
    }
    $thumb-color: #1d19ff;
    $thumb-height: 30px;
    $thumb-width: 8px;
    $track-width: 136px;
    $track-height: 6px;
  
    $track-border-width: 1px;
    $track-border-color: black;
  
    @mixin track() {
      width: $track-width;
      height: $track-height;
      cursor: pointer;
      animate: 0.2s;
    }
  
    @mixin thumb() {
      height: $thumb-height;
      width: $thumb-width;
      background: $thumb-color;
      cursor: pointer;
      border: none;
      border-radius: 0;
    }

    margin: 0;
    -webkit-appearance: none;
    width: $track-width;
    height: 64px;

    &:focus {
      outline: none;
    }

    &::-webkit-slider-runnable-track {
      @include track();
      @include track-color();
    }

    &::-webkit-slider-thumb {
      @include thumb();
      -webkit-appearance: none;
      margin-top: ((-$track-border-width * 2 + $track-height) / 2) - ($thumb-height / 2);
    }

    &:focus::-webkit-slider-runnable-track {
      @include track-color();
    }

    &::-moz-range-track {
      @include track();
      @include track-color();
    }

    &::-moz-range-thumb {
      @include thumb();
    }

    &::-ms-track {
      @include track();
      background: transparent;
      border-color: transparent;
      border-width: $thumb-width 0;
      color: transparent;
    }

    &::-ms-fill-lower {
      @include track-color();
    }

    &::-ms-fill-upper {
      @include track-color();
    }

    &::-ms-thumb {
      @include thumb();
    }

    &:focus::-ms-fill-lower {
      @include track-color();
    }

    &:focus::-ms-fill-upper {
      @include track-color();
    }
  }
}