.mz {
  &-screen {
    position: relative;
  }
  &-trackname {
    position: absolute;
    font-size: 18px;
    color: #fff;
    left: 0;
    top: 0;
    margin: 0;
    padding: 12px;
    background: -moz-linear-gradient(45deg, #ff325f 0%, #2671e2 100%);
    background: -webkit-linear-gradient(45deg, #ff325f 0%,#2671e2 100%); 
    background: linear-gradient(45deg, #ff325f 0%,#2671e2 100%); 
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;
    z-index: 100;

    @media (max-width: 600px) {
      font-size: 14px;
    }
  }
  &-canvas {
    position: relative;
    display: block;
    margin: auto;
    width: 100%;
    height:512px;
    background-color: #14003D;
    background-repeat: no-repeat;
    background-position: right bottom;

    @media (max-height: 800px) {
      height: 300px;
      background-size: auto 260px;
    }
    @media (max-width: 900px) {
      height: 300px;
      background-size: auto 260px;    }
  }
  &-control-panel {
    display: flex;
    justify-content: center;
    padding: 12px;
    height: 100%;
  }
  &-bottom {
    margin: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 200px;
  }

  &-controls {
    position: relative;
    width: 64px;
    height: 64px;
    margin: 0 12px;
    background: none;
    border: 6px solid #14003D;
}
  &-play{
    &:before {
      position: absolute;
      content: "";
      width: 0;
      height: 0;
      border-top: 16px solid transparent;
      border-bottom: 16px solid transparent;
      border-left: 24px solid #14003D;
      top: 10px;
      left: 18px;
    }
  }
  &-stop{
    &:before {
      position: absolute;
      content: "";
      width: 24px;
      height: 24px;
      background-color: #14003D;
      top: 14px;
      left: 14px;
    }
  }

  &-pause{
    &:before {
      position: absolute;
      content: "";
      width: 10px;
      height: 24px;
      background-color: #14003D;
      top: 14px;
      left: 14px;
    }
    &:after {
      position: absolute;
      content: "";
      width: 10px;
      height: 24px;
      background-color: #14003D;
      top: 14px;
      left: 28px;
    }
  }

  &-loading {
   
    width: 32px;
    min-height: 32px;
    height: 32px;
    margin: 0 auto 16px;
    border: 6px solid #14003D;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    0% {transform: rotateZ(0deg)}
    100% {transform: rotate(360deg)}  
  }

  &-change-visual {
    position: relative;
    width: 64px;
    height: 64px;
    margin: 0 12px;
    display: block;
    padding: 8px 0px 12px;
    border: none;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: bold;
    border-left: 4px solid #ff325f;
    border-right: 4px solid #1d19ff;
    background: none;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 4px;
      top: 0;
      left: 0;
      background: -moz-linear-gradient(45deg, #ff325f 0%, #1d19ff 100%);
      background: -webkit-linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
      background: linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
    }
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 4px;
      bottom: 0;
      left: 0;
      background: -moz-linear-gradient(45deg, #ff325f 0%, #1d19ff 100%);
      background: -webkit-linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
      background: linear-gradient(45deg, #ff325f 0%,#1d19ff 100%); 
    }
  }

  &-timeline-placeholder {
    height: 24px;
    background-color: #000;
  }
}